.App {
  padding: 50px;
}

.flip-book {
  box-shadow: 0 0 20px 0 rgba(223, 9, 9, 0.795);
  display: none;
  background-size: cover;
}

.page {
  box-shadow: 0 0 20px 0 rgba(27, 26, 26, 0.795);
  background-color: rgb(32, 31, 29);
  color: hsl(35, 35, 35);
  border: solid 1px rgba(238, 162, 11, 0.596);
  overflow: hidden;
}

.page-header {
  height: 30px;
  font-size: 100%;
  text-transform: uppercase;
  text-align: center;
  margin: 20px;
}

.page-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.page-image {
  height: 100%;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

.page-text {
  padding: 20px;
  height: 100%;
  flex-grow: 1;
  font-size: 80%;
  text-align: center;
  margin-top: 10px;
  padding-top: 10px;
  box-sizing: border-box;
  border-top: solid 1px hsl(35, 55, 90);
}

 .page-footer {
  height: 30px;
  border-top: solid 1px hsl(35, 55, 90);
  font-size: 50%;
  color: hsl(35, 20, 50);
  text-align: center;
}

.page-cover {
  background-color: hsl(35, 45, 80);
  color:  hsl(35, 35, 35);
  border: solid 1px hsl(35, 20, 50);

}
.page-cover h2 {
  text-align: center;
  padding-top: 50%;
  font-size: 210%;
}